import React from 'react';
import { DatePicker, Button } from 'antd';
import axios from 'axios'
import backendURL from '../../consts';


const {MonthPicker} = DatePicker;


class MetricsMain extends React.Component {
    state = {
        month: ''
    }
    onChangeMonth = (date, dateString) => {
        this.setState({
            month: dateString
        })
        console.log(this.state.month)
      }

    fetch = () => {
        axios.post(`${backendURL}/metrics/month/`, {
            month: this.state.month,
        })
        .then(res => {
            this.setState({
                month: '2019-10',
            })
        })
    }

    //////////////////////////
    test = () => {
        axios.post(`${backendURL}/metrics/test_tasks/`)
        .then(res => {
            this.setState({
                month: '2019-10',
            })
        })
    }
    //////////////////////////


    render () {
        return(
            <div>
                <MonthPicker onChange={this.onChangeMonth} placeholder=" " />
            <Button type="primary" style={{'marginLeft': '20px'}} onClick={this.fetch}>OK</Button>
            <Button type="default" style={{'marginLeft': '20px'}} onClick={this.test}>Test</Button>

                {/* <Button onClick={this.fetch}></Button> */}
            </div>
        )
    }

}

export default MetricsMain;